import Button from "./packages/button";

const components = {
  Button
};

const install = function(Vue, options = {}) {
  Object.values(components).map(component => {
    Vue.component(component.name, component);
  });
};

if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}

export default {
  install,
  ...components
};
